export default class Page {
    constructor() {
        this.name = "Page 1";
        this.fields = {};
        this.static = false;

        this.is_updating = false;

        this["preview-image"] = "preview/...";
        this["thumb-image"] = "thumb/...";
        this["width-in"] = 1.496062992126;
        this["height-in"] = 1.259842519685;
        this["width-cm"] = 3.8;
        this["height-cm"] = 3.2;
        this["preview-url"] = "https://domain.tld/web-to-print/preview/get/guid/1234.jpg/";
        this["thumb-url"] = "https://domain.tld/web-to-print/thumbnail/get/guid/1234.jpg/width/100/height/100/";
        this.shapes = {
            "Photo": {
                "name": "Photo",
                "x1": 0.0388,
                "y1": 0.0468,
                "x2": 0.9612,
                "y2": 0.879,
                "anchor-x": 0.748,
                "anchor-y": 0.6766,
                "hidden": false,
                "has-value": false
            }
        };
        this.images = {
            "Photo": {
                "name": "Photo",
                "width": 414,
                "height": 314,
                "color-picker": null,
                "allow-upload": true,
                "allow-url": false,
                "clipped": false,
                "palette": null,
                "value": null
            }
        }
    }
}



